import React from 'react'
import { Section, Heading, Text, Button } from 'src/components/Base'
import { Grid, Row, Col } from 'react-flexbox-grid'
import { RichText } from 'prismic-reactjs'
import PropTypes from 'prop-types'
import { v4 as uuid } from 'uuid'

import styles from './styles.module.scss'

const MembershipPrices = ({
  heading,
  text,
  priceTableRows,
  pdfUrl,
  ...props
}) => {
  const renderedTableRows = priceTableRows.map((row) => {
    console.log(row)

    return (
      <Row key={uuid()} className={styles.row}>
        <Col xs={6} className={styles.left}>
          <Text className={styles.text}>
            {RichText.asText(row.table_start.raw)}
          </Text>
        </Col>
        <Col xs={6} className={styles.right}>
          <Text className={styles.text}>
            {RichText.asText(row.table_end.raw)}
          </Text>
        </Col>
      </Row>
    )
  })

  return (
    <Section narrow {...props}>
      <Heading as="h2" size="lg">
        {RichText.asText(heading.raw)}
      </Heading>
      <Text>{RichText.asText(text.raw)}</Text>
      <Grid className={styles.grid}>{renderedTableRows}</Grid>
      <Button to={pdfUrl} download target="blank">
        Mitglied werden
      </Button>
    </Section>
  )
}

MembershipPrices.propTypes = {
  heading: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  text: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  priceTableRows: PropTypes.arrayOf(
    PropTypes.shape({
      raw: PropTypes.array,
      text: PropTypes.string,
      html: PropTypes.string,
    })
  ),
}

export default MembershipPrices
